import { Component, OnInit } from '@angular/core';
import { appUrl } from 'src/app/app-url.module';
import { IShopQR } from 'src/app/models/qr-code.model';
import { BankService } from 'src/app/services/bank.service';
import { imagePath } from 'src/environments/environment.prod';

@Component({
  selector: 'app-bank',
  templateUrl: './bank.page.html',
  styleUrls: ['./bank.page.scss'],
})
export class BankPage implements OnInit {
  banks: IShopQR[] = [];
  url = appUrl;
  imgPath = imagePath;

  constructor(private bankService: BankService) {}

  ngOnInit() {
    this.onFetchData();
  }

  isRefresh(evt: any) {
    setTimeout(() => {
      this.onFetchData();
      evt.target.complete();
    }, 1000);
  }

  onFetchData() {
    this.bankService.getAllBank().then((observe) => {
      observe.subscribe({
        next: (data) => {
          this.banks = data;
        },
        error: () => {},
        complete: () => {},
      });
    });
  }
}
